import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SacramentComponent } from './sacrament.component';
import { BroadcastProgramComponent } from './broadcast-program/broadcast-program.component';
import { InPersonProgramComponent } from './in-person-program/in-person-program.component';

const routes: Routes = [
  {
    path: '',
    component: SacramentComponent,
    children: [
      {
        path: 'broadcast',
        component: BroadcastProgramComponent,
      },
      {
        path: 'in-person',
        component: InPersonProgramComponent,
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SacramentProgramRoutingModule {}
